.d__row_disable{
    cursor: default!important;
}

.design__tc_row{
    display: flex;
    flex: 0 0 76.5%;
    align-items: center;

    .objname_col{
        flex: 0 0 23.5%;
    }
    
    .keyword_col{
        flex: 0 0 24%;
    }
    
    .input_col{
        flex: 0 0 26%;
    }
    
    .output_col{
        flex: 0 0 27%;
    }
}

.d__ghost_row{
    opacity: 0;
}

.d__table_row{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
    margin-right: 2px;
    font-size: 14px;
    font-family: "LatoWeb";
    border-left: 5px solid transparent;
    cursor: pointer;

    span:not(.remark_col):not(.details_col):not(.step_col){
        text-align: left;
    }
}

.d__odd_row{
    background-color: #e8e6fe;
}



.commented_row{
    border-left: 5px solid red;
    .output_col {
        color: red;
    }
}

.highlight-step{
    background-color: #e3caff;
}


.col_select, .col_inp{
    height: 25px;
    width: 95%;
    padding: 0 10px;
    border: 1px solid #5e636b;
    font-size: 12px;
    color: #555;
    border-radius: 4px;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    outline: none;
    &:focus{
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgb(204, 169, 255);
    }
}
.d__row_text{
    min-height: 20px;
    width: 95%;
    overflow: hidden;
    overflow-wrap: anywhere;
    text-overflow: ellipsis;
    margin: 0 6px;
}


.col_inp_area{
    min-height: 25px;
    max-height: 25px;
    resize: none;
}

.d__disabled_step{
    pointer-events: none;
    cursor: default;
}


