.tr__body{
    &.bottom-content {
        // top: 377px;
        // height: 150px;
        width: 100%;
        margin-top: 25px;
        // position: absolute;
    }
    &.rp__content{
        width: 100%;
        // position: absolute;
        height: 255px;
        // top: 70px;
        margin-top: 20px;
        .left-content{
            height: 100%;
            width: 50%;
            float: left;
            padding-right: 30px;
        }
        .right-content{
            height: 100%;
            width: 50%;
            float: right;
            padding-left: 30px;
        }
    }
    .panel {
        margin-bottom: 20px;
        background-color: #fff;
        border: 1px solid transparent;
        border-radius: 4px;
        -webkit-box-shadow: 0 1px 1px rgba(0,0,0,.05);
        box-shadow: 0 1px 1px rgba(0,0,0,.05);
        border-color: #ddd;
    }
}