.ss__btngroup{
    display: flex;
    max-width: 100%;
    margin-bottom: 15px;
    margin-left: 36px;
    justify-content: space-between;
    font-family: 'LatoWebLight';
}

.ss__left-btns{
    border-bottom: 1px solid #707070;
    padding-bottom: 10px;
    display: flex;
    align-items: center;
}

.ss__select-all{
    display: flex;
    align-items: center;
    margin: 0;
}

.ss__select-all-chkbox{
    height: 15px;
    width: 15px;
}

.ss__select-all-lbl{
    margin: 0 12px;
    font-family: 'LatoWeb';
    color: #33363B;
    font-size: 16px;
}

.ss__taskBtn{
    padding: 1px 13px;
    margin-right: 7px;
    height: 21px;
    font-size: 13px;
    background-color: #633693;
    &:disabled{
        background-color: #997cb8;
        cursor: not-allowed;
    }
}

.ss__btn{
    border: none;
    outline: none;
    color: #fff;
    border-radius: 20px;
}

.ss__search-btn{
    outline: none;
    color: #fff;
    background-color: #fff;
    display: flex;
    border: 1px solid #633693;
    border-radius: 5px;
    height: 24px;
}

.ss__search-icon{
    vertical-align: baseline;
}

.ss__search_field{
    margin-left: 3px;
    padding: 3px;
    border: 1px solid #633693;
    border-radius: 5px;
    width: 200px;
    height: 24px;
    font-family: "LatoWeb";
    font-size: 13px;
    &:focus{
      outline: none;  
    }
}

.scraped_obj_list{
    display: flex;
    position: relative;
    flex-direction: column;
    margin-left: 5px;
    height: 100%;
}

.sc__ab{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
}
.sc__min{
    min-height: 0;
}
.sc__con{
    display: flex;
    flex-direction: column;
    flex: 1 1;
    height: 100%;
}

.scrape_object_container{
    flex: 1 1;
    overflow: auto;
}

.ss__dup_scroll{
    display: flex;
    flex-direction: column;
}
.ss__dup_labels{
    display: flex;
    flex-direction: column;
    font-family: "LatoWeb";
    font-size: 14px;
    font-weight: bold;
    color: black;
    max-height: 250px;
}
.ss__dup_li{
    font-weight: normal;
}